<template>
    <v-card class="mx-auto ma-5" max-width="1000px" :flat="$vuetify.breakpoint.smAndDown">
        <div>
            <div class="pa-1 d-inline-flex justify-center align-center" style="width: 100%">
                <div style="width: 120px" class="me-1">
                    <v-overflow-btn outlined hide-details v-model="originalSelection" :items="originalSelections"/>
                </div>
                <v-text-field v-model="blogArticle.title" outlined clearable label="标题" hide-details/>
            </div>
            <v-divider/>
            <v-textarea outlined class="ma-2" v-model="blogArticle.articleAbstract" clearable label="摘要" hide-details/>
            <v-divider/>
            <EditorMD @transferContent="getContent" :pre-set-content="paramContent" :read-only="false"/>
            <v-row>
                <v-col class="ps-5 pt-0 pb-3">
                    <v-combobox :loading="loadingTypes" flat v-model="blogArticle.typeString" :items="types" chips
                                clearable label="分类" prepend-inner-icon="mdi-view-list" solo hide-details>
                        <template v-slot:selection="{ attrs, item, select, selected }">
                            <v-chip v-bind="attrs" :input-value="selected" close @click="select"
                                    @click:close="removeType">
                                <strong>{{ item }}</strong>
                            </v-chip>
                        </template>
                    </v-combobox>
                </v-col>
                <v-col class="pe-5 pt-0 pb-3">
                    <v-combobox :loading="loadingTags" flat v-model="blogArticle.tags" :items="tags" chips clearable
                                label="标签" multiple prepend-inner-icon="mdi-tag-multiple" solo hide-details>
                        <template v-slot:selection="{ attrs, item, select, selected }">
                            <v-chip v-bind="attrs" :input-value="selected" close @click="select"
                                    @click:close="removeTag(item)">
                                <strong>{{ item }}</strong>
                            </v-chip>
                        </template>
                    </v-combobox>
                </v-col>
            </v-row>
            <v-divider/>
            <v-text-field outlined class="pa-1" v-model="blogArticle.imgUrl" clearable hide-details label="首图地址"/>
            <v-divider/>
            <div class="d-flex">
                <v-checkbox class="ps-5" v-model="blogArticle.recommend" label="推荐"/>
                <v-checkbox class="ps-5" v-model="blogArticle.canReward" label="打赏"/>
                <v-checkbox class="ps-5" v-model="blogArticle.canComment" label="评论"/>
                <v-checkbox class="ps-5" v-model="blogArticle.show" label="展示"/>
                <v-checkbox class="ps-5" v-model="blogArticle.deleted" label="删除"/>
            </div>
        </div>
    </v-card>
</template>

<script>
    import EditorMD from "@/components/admin/EditorMD";

    export default {
        name: "EditArticle",
        props: {
            preSetBlogArticle: {
                type: Object
            }
        },
        components: {
            EditorMD
        },
        data() {
            return {
                blogArticle: {
                    title: null,
                    imgUrl: null,
                    articleAbstract: null,
                    content: null,
                    show: true,
                    deleted: false,
                    typeString: null,
                    tags: [],
                    original: true,
                    recommend: false,
                    canReward: true,
                    canComment: true
                },

                originalSelections: ["原创", "转载"],
                originalSelection: "原创",
                types: [],
                tags: [],

                paramContent: "",
                loadingTypes: true,
                loadingTags: true
            }
        },
        mounted() {
            this.getAllTypes()
            this.getAllTags()
        },
        methods: {
            removeType() {
                this.blogArticle.typeString = ""
            },
            removeTag(item) {
                this.blogArticle.tags.splice(this.blogArticle.tags.indexOf(item), 1)
                this.blogArticle.tags = [...this.blogArticle.tags]
            },
            getContent(content) {
                this.blogArticle.content = content
            },
            getAllTypes() {
                let _this = this
                this.axios.get(this.API.URL.QUERY_TYPE_WITH_COUNT, {headers: {'Content-Type': 'multipart/form-data'}})
                    .then(function (result) {
                        _this.loadingTypes = false
                        let data = result.data
                        if (data.success) {
                            // console.log("data", data)
                            _this.types = Object.keys(data.data)
                        } else {
                            console.log("dataError", result)
                        }
                    }, function (error) {
                        _this.loadingTypes = false
                        console.log(error)
                    })
            },
            getAllTags() {
                let _this = this
                this.axios.get(this.API.URL.QUERY_TAG_WITH_COUNT, {headers: {'Content-Type': 'multipart/form-data'}})
                    .then(function (result) {
                        _this.loadingTags = false
                        let data = result.data
                        if (data.success) {
                            // console.log("data", data)
                            _this.tags = Object.keys(data.data)
                        } else {
                            console.log("dataError", result)
                        }
                    }, function (error) {
                        _this.loadingTags = false
                        console.log(error)
                    })
            }
        },
        watch: {
            originalSelection(val) {
                this.blogArticle.original = val === "原创"
            },
            preSetBlogArticle(val) {
                this.blogArticle = val
                this.paramContent = val.content
            },
            blogArticle: {
                handler(new_value) {
                    console.log("编辑文章", new_value)
                    this.$emit("transferBlogArticle", new_value)
                },
                deep: true
            }
        }
    }
</script>

<style scoped>

</style>