<template>
    <div>
        <EditArticle :pre-set-blog-article="paramBlogArticle" @transferBlogArticle="getBlogArticle"/>
        <div class="d-flex justify-end align-center" style="position: fixed; bottom: 0; right: 0; z-index: 10">
            <v-slide-x-reverse-transition>
                <v-alert v-if="paramBlogArticle===undefined?alertConfig.alertOpen:true" v-model="alertConfig.alertOpen"
                         class="ma-5" :type="alertConfig.alertType" elevation="5" dismissible>
                    {{paramBlogArticle===undefined?alertConfig.alertMessage:"正在编辑文章"+paramBlogArticle.id}}
                </v-alert>
            </v-slide-x-reverse-transition>
            <v-btn fab :loading="loading" large class="ma-5" color="primary" @click="insertOrUpdate">
                {{paramBlogArticle===undefined?"发布":"更新"}}
            </v-btn>
        </div>
        <v-snackbar v-model="snackbar" color="success">
            {{paramBlogArticle===undefined?"文章发布成功":"文章更新成功"}}
            <!--            <template v-slot:action="{ attrs }">-->
            <!--                <v-btn color="pink" text v-bind="attrs"-->
            <!--                       @click="$router.push({name: 'article', params: {id: blogArticle.id} })">-->
            <!--                    查看-->
            <!--                </v-btn>-->
            <!--            </template>-->
        </v-snackbar>
    </div>
</template>

<script>
    import EditArticle from "@/components/admin/article/EditArticle";

    export default {
        name: "NewArticle",
        props: {},
        components: {
            EditArticle
        },
        data() {
            return {
                loading: false,
                paramBlogArticle: undefined,
                blogArticle: undefined,
                alertConfig: {
                    alertOpen: false,
                    alertType: "info",
                    alertMessage: ""
                },
                snackbar: false
            }
        },
        mounted() {
        },
        beforeDestroy() {
        },
        methods: {
            insertOrUpdate() {
                if (this.paramBlogArticle !== undefined) {
                    this.update()
                } else {
                    this.insert()
                }
            },
            getBlogArticle(blogArticle) {
                this.blogArticle = blogArticle
            },
            insert() {
                if (this.blogArticle === undefined) {
                    this.alertConfig = {
                        alertOpen: true,
                        alertType: "error",
                        alertMessage: "文章对象为空"
                    }
                } else {
                    this.loading = true
                    let _this = this
                    this.axios.put(
                        this.API.URL.INSERT_ARTICLE,
                        JSON.stringify(this.blogArticle),
                        {
                            headers: {'Content-Type': 'application/json'},
                        })
                        .then(function (result) {
                            _this.loading = false
                            let data = result.data
                            if (data.success) {
                                console.log("data", data)
                                // _this.blogArticle.id = data.data.id
                                _this.blogArticle = undefined
                                _this.snackbar = true
                            } else {
                                console.log("dataError", result)
                                _this.alertConfig = {
                                    alertOpen: true,
                                    alertType: "error",
                                    alertMessage: data.errorMsg
                                }
                            }
                        }, function (error) {
                            _this.loading = false
                            console.log(error)
                        })
                }
            },
            update() {
                this.loading = true
                let _this = this
                this.axios.post(
                    this.API.URL.UPDATE_ARTICLE,
                    JSON.stringify(this.paramBlogArticle),
                    {
                        headers: {'Content-Type': 'application/json'},
                    })
                    .then(function (result) {
                        _this.loading = false
                        let data = result.data
                        if (data.success) {
                            console.log("data", data)
                            // _this.blogArticle.id = data.data.id
                            _this.paramBlogArticle = data.data
                            _this.snackbar = true
                        } else {
                            console.log("dataError", result)
                            _this.alertConfig = {
                                alertOpen: true,
                                alertType: "error",
                                alertMessage: data.errorMsg
                            }
                        }
                    }, function (error) {
                        _this.loading = false
                        console.log(error)
                    })
            }
        },
        watch: {
            "$route": {
                handler() {
                    setTimeout(() => {
                        if (this.$route.params.article !== undefined) {
                            console.log(this.$route)
                            this.paramBlogArticle = this.$route.params.article
                        }
                    }, 300)
                },
                // 首次进入页面即执行
                immediate: true
            }
        }
    }
</script>

